module.exports = "## Pagination\r\n\r\n分页器。\r\n\r\n## 使用\r\n\r\n```html\r\n<m-pagination total=\"125\" current-page=\"1\" page-size=\"5\" id=\"myPagination\">\r\n</m-pagination>\r\n\r\n<script>\r\n  var pagination = document.querySelector('#myPagination')\r\n  pagination.addEventListener('change', function (evt) {\r\n    console.log(evt.detail)\r\n  })\r\n</script>\r\n```\r\n\r\n## Omi 中使用\r\n\r\n```jsx\r\n<m-pagination\r\n  total={125}\r\n  currentPage={1}\r\n  pageSize={5}\r\n  onChange={this.onChange}\r\n/>\r\n```\r\n\r\n## API\r\n\r\n### Props\r\n\r\n```jsx\r\n{\r\n\ttotal: number,\r\n\tpageSize: number,\r\n\tnumDisplay: number,\r\n\tcurrentPage: number,\r\n\tnumEdge: number,\r\n\tlinkTo: string,\r\n\tprevText: string,\r\n\tnextText: string,\r\n\tellipseText: string,\r\n\tprevShow: boolean,\r\n\tnextShow: boolean\r\n}\r\n```\r\n\r\n### Default Props\r\n\r\n```jsx\r\n{\r\n  total: 0,\r\n  pageSize: 10,\r\n  numDisplay: 5,\r\n  currentPage: 0,\r\n  numEdge: 3,\r\n  linkTo: '#',\r\n  prevText: 'Prev',\r\n  nextText: 'Next',\r\n  ellipseText: '...',\r\n  prevShow: true,\r\n  nextShow: true\r\n}\r\n```\r\n"

